import React, { useContext } from 'react'
import './Cart.css'
import { StoreContext } from '../../Context/SetContext'
import { useNavigate } from 'react-router-dom'

function Cart() {
  const { certItem, food_list, removeFromecart,getTotalCartAmount } = useContext(StoreContext)
   const navigate = useNavigate();
  return (
    <div className='cart'>
      <div className="cart-items">
        <div className="cart-items-title">
          <p>Items</p>
          <p>Title</p>
          <p>Price</p>
          <p>Quantity</p>
          <p>Total</p>
          <p>Remove</p>
        </div>
        <br />
        <hr />
        {food_list.map((item, index) => {
          if (certItem[item._id] > 0) {
            return (
              <div>
                <div className="cart-items-title cart-items-item">
                  <img src={item.image} alt="" />
                  <p>{item.name}</p>
                  <p>${item.price} </p>
                  <p>{certItem[item._id]}</p>
                  <p>${item.price * certItem[item._id]}</p>
                  <p onClick={() => removeFromecart(item._id)} className='cross'>X</p>
                </div>
                <hr />
              </div>


            )
          }
        })}
      </div>
      <div className="cart-bottom">
        <div className="cart-total">
          <h2>Cart Totals</h2>
          <div>
            <div className="cart-totals-details">
              <p>Subtotal</p>
              <p>${getTotalCartAmount()}</p>
            </div>
            <hr />
            <div className="cart-totals-details">
              <p>Delivary Free</p>
              <p>${getTotalCartAmount()===0?0:2}</p>
            </div>
            <hr />
            <div className="cart-totals-details">
              <p>Total</p>
              <p>${getTotalCartAmount()===0?0:getTotalCartAmount()+2}</p>
            </div>
          </div>
          <button onClick={()=>navigate('/order')}>PROCEED TO CHECKOUT</button>
        </div>

        <div className="cart-promocode">
          <div>
            <p> If you have promo code, Enter it here</p>
            <div className="cart-promocode-input">
              <input type="text" placeholder='Promo Code' />
              <button>submit</button>
            </div>
          </div>
        </div>
      </div>

    </div>

  )
}

export default Cart
